class BackgroundGenerator {
  constructor() {
    this.appElement = document.querySelector(".App");

    this.buttonElement = document.querySelector(".button");
    this.buttonElement.addEventListener("click", () => {
      this.setNewBackgroundColor();
    });

    this.generatedColorTextElement = document.querySelector(
      ".text_generatedColor"
    );
  }

  setNewBackgroundColor() {
    let rgba = `rgba(${this.generateNewBackground()}, 1)`;
    this.appElement.style.backgroundColor = rgba;

    this.generatedColorTextElement.style.backgroundColor = rgba;
    this.generatedColorTextElement.innerHTML = rgba;

    this.buttonElement.style.backgroundColor = `rgba(${this.generateNewBackground()}, 1)`;
  }

  generateNewBackground() {
    console.log("generating");
    let newBackgroundColor = [];
    for (let i = 0; i < 3; i++) {
      newBackgroundColor.push(Math.round(Math.random() * 255));
    }
    return newBackgroundColor.toString();
  }
}

const backgroundGenerator = new BackgroundGenerator();
//freeze object no longer allowing modifications
//Singleton pattern - allow only one instance
Object.freeze(backgroundGenerator);
